let id = 0;
function createData(featureID, enabled, backBtn, homeBtn, reloadBtn, url, action) {
    id += 1;
    return { id, featureID, enabled, backBtn, homeBtn, reloadBtn, url, action};
}

const rows = [
    createData('io.tulip.sat.products', true, false, false, false, null, 'Edit'),
    createData('io.tulip.sat.customers', true, false, false, false, null, 'Edit'),
    createData('io.tulip.associates', true, false, false, false, null, 'Edit'),
    createData('io.tulip.stores', true, false, false, false, null, 'Edit'),
    createData('io.tulip.sat.stores', false, true, true, true, '../testing', 'Edit'),
    createData('io.tulip.sat.clienteling.sms.inbox', true, false, false, false, null, 'Edit'),
    createData('io.tulip.sat.clienteling.followups', true, false, false, false, null, 'Edit'),
];

function showDoc(url) {
    return (
        <iframe src="http://10.0.79.26:5000/#/clienteling/modules# + {url}" width="480"></iframe>
    );
}

function Modules(props) {

    const { classes } = props;

    const [state, setState] = React.useState({
        docPanel: false,
        editPanel: false,
      });
  
    const [values, setValues] = React.useState({
        enabled: true,
        featureID: 'io.tulip.sat.customers',
        name: '',
        category: '',
        type: '',
        path: '',
        backBtn: true,
        homeBtn: true,
        reloadBtn: true,

    });

    const handleChange = name => event => {
       setValues({ ...values, [name]: event.target.value });
    };


    const handleChangeCheckbox = name => event => {
        setValues({ ...values, [name]: event.target.checked });
    };


    const toggleDrawer = (drawer, status, row) => () => {
        setState({ ...state, [drawer]: status });
        if (status){
            setValues({
                ...values, 
                ['featureID']: row.featureID,
                ['category']: null,
                ['name']: null,
                ['type']: null,
                ['path']: row.url,
                ['backBtn']: row.backBtn,          
                ['homeBtn']: row.homeBtn,    
                ['reloadBtn']: row.reloadBtn,                        
            });
        }
    };

    const toggleDocView = (view) => () => {
    console.log("enter toggleDocView");
    setState({ ...state, [view]: !state.docPanel });
    };

    const docPanel = (
    <div className={classes.docContainer}>
        <iframe src="http://10.0.79.26:5000/#/clienteling/modules# + {url}" width="480" height='100%'></iframe>           
    </div>
    );

    const editPanel = (
      <div className={classes.root}>
        <div className={classes.list}>
          <div className={classes.panelHeader}>
            <Typography className={classes.h2} variant="h5"> {values.featureID} </Typography>
          </div>
          <SizeMe monitorHeight>{({ size }) =>
          <form className={classes.container} noValidate autoComplete="off" id="formContainer">
            <div className={classes.formContainerLeft}>
              <div className={classes.formTitle}>
                <Typography className={classes.h3} variant="h6"> Basic Settings </Typography>
              </div>
              <TextField
                id="featureID"
                label="Feature ID"
                className={classes.textField}
                value={values.featureID}
                onChange={handleChange('featureID')}
                margin="normal"
                variant="outlined"
                disabled
              />
              <TextField
                id="category"
                label="Category"
                className={classes.textField}
                value={values.category}
                onChange={handleChange('category')}
                margin="normal"
                variant="outlined"
              />
              <TextField
                id="name"
                label="Module Name"
                className={classes.textField}
                value={values.name}
                onChange={handleChange('name')}
                margin="normal"
                variant="outlined"
              />
              <TextField
                id="type"
                label="Module Type"
                className={classes.textField}
                value={values.type}
                onChange={handleChange('type')}
                margin="normal"
                variant="outlined"
              />
              <TextField
                id="path"
                label="Path"
                className={classes.textField}
                value={values.path}
                onChange={handleChange('path')}
                margin="normal"
                variant="outlined"
              />

              <FormControl component="fieldset" className={classes.formControl}>
                  <Typography variant="subtitle1">Navigation Options</Typography>
                  <FormHelperText>Applied to web view only</FormHelperText>
                  <FormGroup>
                      <FormControlLabel className={classes.formCheckbox}
                          control={
                              <Checkbox checked={values.backBtn} value="backBtn" onChange={handleChangeCheckbox('backBtn')}/>
                          }
                          label="Show Back Button"
                      />
                      <FormControlLabel className={classes.formCheckbox}
                          control={
                              <Checkbox checked={values.homeBtn} value="homeBtn" onChange={handleChangeCheckbox('homeBtn')}/>
                          }
                          label="Show Home Button"
                          />
                      <FormControlLabel className={classes.formCheckbox}
                          control={
                              <Checkbox checked={values.reloadBtn} value="reloadBtn" onChange={handleChangeCheckbox('reloadBtn')}/>
                          }
                          label="Show Reload Button"
                      />
                  </FormGroup>
                  </FormControl>

            </div>
            <div className={classes.formContainerRight}>
              <div className={classes.formTitle}>
                <Typography className={classes.h3} variant="h6"> Advanced Settings </Typography>
              </div>
              <TextField
                id="outlined-listtype"
                label="List Type"
                className={classes.textField}
                value="3 (Store Book)"
                onChange={handleChange('name')}
                margin="normal"
                variant="outlined"
              />
            </div>
            <div hidden={!state.docPanel}>
              <div
                tabIndex={0}
                role="button"
              >
              <div className={classes.docContainer}>
                  <iframe src="http://10.0.79.26:5000/#/clienteling/modules# + {url}" width="480" height={size.height}></iframe>
              </div>
              </div>
            </div>
            <Grid containter className={classes.panelBottom}>
              <Grid item xs={6} className={classes.viewDoc}>
                <Button className={classes.btnLink} onClick={toggleDocView('docPanel')}>{ state.left ? 'Hide Docs' : 'View Docs' }</Button>
              </Grid>
              <Grid item xs={6} className={classes.bottomActions}>
                <Button className={classes.btnSecondary} onClick={toggleDrawer('editPanel', false)}>Cancel</Button>
                <Button className={classes.btnPrimary} onClick={toggleDrawer('editPanel', false)}>Save</Button>
              </Grid>
            </Grid>
          </form>}
          </SizeMe>

        </div>
        </div>
    );

    const closeAllDrawers = () => () => {
      setState({ ...state, ['docPanel']: false});
      setState({ ...state, ['editPanel']: false});
    };

    return (
      <div className={classes.root}>
        <TopNav />
        <SideNav />
        <main className={classes.content}>
        <div className={classes.pageHeader}>
          <Typography className={classes.h1} variant="h5">Application Modules</Typography>  
        </div>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell>Feature ID</TableCell>
              <TableCell align="right">Enabled</TableCell>
              <TableCell align="right">Back Button</TableCell>
              <TableCell align="right">Home Button</TableCell>
              <TableCell align="right">Reload Button</TableCell>
              <TableCell align="right">URL Path</TableCell>
              <TableCell align="right">Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map(row => (
              <TableRow key={row.id}>
                <TableCell component="th" scope="row">{row.featureID}</TableCell>
                <TableCell align="right"><Checkbox checked={row.enabled} disabled value="Enabled" /></TableCell>
                <TableCell align="right"><Checkbox checked={row.backBtn} disabled value="Back Button" /></TableCell>
                <TableCell align="right"><Checkbox checked={row.homeBtn} disabled value="Home Button" /></TableCell>
                <TableCell align="right"><Checkbox checked={row.reloadBtn} disabled value="Reload Button" /></TableCell>
                <TableCell align="right">{row.url}</TableCell>
                <TableCell align="right"><Button className={classes.btnLink} onClick={toggleDrawer('editPanel', true, row)}>{row.action}</Button></TableCell>  
                {/* <TableCell align="right"><Button className={classes.btnLink} onClick={openEditPanel(row.id)}>{row.action}</Button></TableCell>   */}
              </TableRow>
            ))}
          </TableBody>
        </Table>
        <Drawer anchor="right" open={state.editPanel} 
            >
          <div
            tabIndex={0}
            role="button"
            className={ state.docPanel ? classes.docOpened : classes.docClosed } 
          >
            {editPanel}
          </div>
        </Drawer>
        </main>
      </div>
    );
  }
  
  Modules.propTypes = {
      classes: PropTypes.object.isRequired,
    };
  
  export default withStyles(styles)(Modules);
  